.row {
  display: flex;
  flex-wrap: wrap;
  padding: 0 4px;
  list-style: none;
}

.column {
  flex: 25%;
  max-width: 25%;
  padding: 0 4px;
}

.column img {
  margin-top: 8px;
  vertical-align: middle;
}
@media screen and (max-width: 800px) {
  .column {
    flex: 50%;
    max-width: 100%;
  }
}

@media screen and (max-width: 600px) {
  .column {
    flex: 100%;
    min-width: 100%;
  }
}
